import React, { useEffect } from 'react';
import styled from 'styled-components';

import { TOP } from '../api/kinopoisk';

import { Loader } from '../components/Loader';
import { FilmListSlider } from '../components/FilmListSlider';

import { useDispatch, useSelector } from 'react-redux';
import { fetchTop } from '../store/topSlice';
import { fetchBest } from '../store/bestSlice';
import { fetchPopular } from '../store/popularSlice';

export const Films = () => {
    const { topList, status: statusTop, error: errorTop } = useSelector((state) => state.top);
    const { bestList, status: statusBest, error: errorBest } = useSelector((state) => state.best);
    const {
        popularList,
        status: statusPop,
        error: errorPop,
    } = useSelector((state) => state.popular);

    const dispatch = useDispatch();
    useEffect(() => {
        dispatch(
            fetchTop({
                name: TOP('TOP_AWAIT_FILMS', 1),
            }),
        );
        dispatch(
            fetchBest({
                name: TOP('TOP_250_BEST_FILMS', 1),
            }),
        );
        dispatch(
            fetchPopular({
                name: TOP('TOP_100_POPULAR_FILMS', 1),
            }),
        );
    }, [dispatch]);

    return (
        <Wrapper>
            <Title>–õ—É—á—à–∏–µ –ø–æ–¥–±–æ—Ä–∫–∏ —Ñ–∏–ª—å–º–æ–≤ –æ—Ç –ö–∏–Ω–æ–ø–æ–∏—Å–∫–∞</Title>
            {statusTop === 'loading' && (
                <Loader>
                    <div className="loader"></div>
                </Loader>
            )}

            {
                <FilmListSlider
                    pathAll="/films/top/"
                    text="–ø–æ—Å–º–æ—Ç—Ä–µ—Ç—å –≤—Å–µ —Ñ–∏–ª—å–º—ã"
                    filmList={topList.films}
                    title="—Ç–æ–ø –æ–∂–∏–¥–∞–µ–º—ã—Ö"
                    sliderName="top"
                    spaceBetween={0}
                    slidesPerGroup={1}
                    slidesPerView={1}
                    path="films"
                    breaks={{
                        550: {
                            spaceBetween: 20,
                            slidesPerView: 2,
                            slidesPerGroup: 2,
                        },
                        768: {
                            spaceBetween: 20,
                            slidesPerView: 3,
                            slidesPerGroup: 3,
                        },
                        1024: {
                            spaceBetween: 20,
                            slidesPerView: 4,
                            slidesPerGroup: 4,
                        },
                        1180: {
                            spaceBetween: 20,
                            slidesPerView: 5,
                            slidesPerGroup: 5,
                        },
                        1366: {
                            spaceBetween: 20,
                            slidesPerView: 6,
                            slidesPerGroup: 6,
                        },
                    }}></FilmListSlider>
            }
            {errorTop && errorTop}
            {statusBest === 'loading' && (
                <Loader>
                    <div className="loader"></div>
                </Loader>
            )}
            <FilmListSlider
                pathAll="/films/best/"
                text="–ø–æ—Å–º–æ—Ç—Ä–µ—Ç—å –≤—Å–µ 250 —Ñ–∏–ª—å–º–æ–≤"
                filmList={bestList.films}
                title="—Ç–æ–ø –ª—É—á—à–∏—Ö"
                sliderName="top"
                spaceBetween={0}
                slidesPerGroup={1}
                slidesPerView={1}
                path="films"
                breaks={{
                    550: {
                        spaceBetween: 20,
                        slidesPerView: 2,
                        slidesPerGroup: 2,
                    },
                    768: {
                        spaceBetween: 20,
                        slidesPerView: 3,
                        slidesPerGroup: 3,
                    },
                    1024: {
                        spaceBetween: 20,
                        slidesPerView: 4,
                        slidesPerGroup: 4,
                    },
                    1180: {
                        spaceBetween: 20,
                        slidesPerView: 5,
                        slidesPerGroup: 5,
                    },
                    1366: {
                        spaceBetween: 20,
                        slidesPerView: 6,
                        slidesPerGroup: 6,
                    },
                }}></FilmListSlider>
            {errorBest && errorBest}

            {statusPop === 'loading' && (
                <Loader>
                    <div className="loader"></div>
                </Loader>
            )}
            <FilmListSlider
                pathAll="/films/popular/"
                text="–ø–æ—Å–º–æ—Ç—Ä–µ—Ç—å –≤—Å–µ 100 —Ñ–∏–ª—å–º–æ–≤"
                filmList={popularList.films}
                title="—Ç–æ–ø –ø–æ–ø—É–ª—è—Ä–Ω—ã—Ö"
                sliderName="top"
                spaceBetween={0}
                slidesPerGroup={1}
                slidesPerView={1}
                path="films"
                breaks={{
                    550: {
                        spaceBetween: 20,
                        slidesPerView: 2,
                        slidesPerGroup: 2,
                    },
                    768: {
                        spaceBetween: 20,
                        slidesPerView: 3,
                        slidesPerGroup: 3,
                    },
                    1024: {
                        spaceBetween: 20,
                        slidesPerView: 4,
                        slidesPerGroup: 4,
                    },
                    1180: {
                        spaceBetween: 20,
                        slidesPerView: 5,
                        slidesPerGroup: 5,
                    },
                    1366: {
                        spaceBetween: 20,
                        slidesPerView: 6,
                        slidesPerGroup: 6,
                    },
                }}></FilmListSlider>
            {errorPop && errorPop}
        </Wrapper>
    );
};

const Wrapper = styled.div`
    width: 100%;
    padding: 1rem 1rem 0;
    background-color: var(--bg-color);

    @media (min-width: 768px) {
        padding: 6rem 1rem 0;
    }
`;

const Title = styled.h1`
    font-size: var(--fz-xl);
    color: var(--colors-text);
    margin: 2rem 0;
    padding: 0;
    text-transform: uppercase;
    word-wrap: break-word;
    @media (min-width: 768px) {
        margin: 0 0 4rem 0;
        font-size: var(--fz-xxl);
    }
`;
